.timer-container{
    width: 100%;
}

.timer-container .title{
    font-size: 40px;
    text-align: center;
}

.length-cont{
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 32px;
}

.length-cont .cont-break{
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.length-cont .cont-break h3{
    margin:0;
    margin-bottom: 5px;
}

.length-cont .cont-break .cont-btn-break{
    width: 40%;
    display: flex;
    justify-content: space-around;

}

.length-cont .cont-session{
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.length-cont .cont-session h3{
    margin:0;
    margin-bottom: 5px;
}

.length-cont .cont-session .cont-btn-session{
    width: 40%;
    display: flex;
    justify-content: space-around;

}

.content-time{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.content-time .cont-timer{
    margin-top: 10px;
    height: 25vh;
    width: 50%;
    border: 10px solid #40739e;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.content-time .cont-timer h3{
    font-size: 30px;
    margin: 0;
}

.content-time .cont-timer span{
    font-size: 70px;
}

.content-time .cont-btn{
    width: 40%;
    margin-top: 17px;
    display: flex;
    justify-content: space-around;
}



button{
    border: 0;
    background-color: #1e3799;
    cursor: pointer;
    color: #fff;
}

svg{
    transform: scale(2.5);
}


